<template>
  <div class="search">
    <div class="menu-search">
      <van-search placeholder="请输入商品名称" shape="round" show-action @input="search" v-model="value">
        <template #action>
          <div @click="back">取消</div>
        </template>
      </van-search>
    </div>
    <div class="menu-product">
      <div
        class="menu-product-list"
        v-for="(item,index) in products"
        :key="index"
       
      >
        <div class="list-box" @click="viewProductInfo(item.pid)">
          <img class="fl" :src="item.smallImg" alt />
          <div class="list-info">
            <div class="list-text fl">
              <div class="ch-name">{{item.name}}</div>
              <div class="en-name">{{item.enname}}</div>
              <div class="list-price">￥{{item.price}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/search.less";
export default {
  data() {
    return {
      value: "",
      products: []
    };
  },
  methods: {
    // 搜索商品
    search() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "get",
        url: "/search",
        params: {
          appkey: this.appkey,
          tokenString,
          name: this.value,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
           if (result.data.code == 'Q001') {
            this.products = [];
            this.products = result.data.result;
             
            //将商品数据缓存,JSON.stringify()将 JavaScript 值转换为 JSON 字符串。
            sessionStorage.setItem(item.type, JSON.stringify(this.products));
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },
    back() {
      this.$router.go(-1);
    },
  //查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>